import React from 'react'

function Carousel() {
    return (
        <section className="banner">
            <div id="carouselExampleInterval" className="carousel slide" data-bs-ride="carousel">

                <ol className="carousel-indicators">
                    <li data-bs-target="#carouselExampleIndicators" data-bs-slide-to="0" className="active"></li>
                    <li data-bs-target="#carouselExampleIndicators" data-bs-slide-to="1"></li>
                    <li data-bs-target="#carouselExampleIndicators" data-bs-slide-to="2"></li>
                </ol>

                <div className="carousel-inner">
                    <div className="carousel-item active" data-bs-interval="10000">
                        <picture>
                            <source media="(min-width: 768px)" srcSet="images/banners/img.jpg"/>
                            <source media="(max-width: 768px)" srcSet="images/banners/sm/img.jpg"/>
                            <img src="images/banners/img.jpg" alt="regular pyramid built from four equilateral triangles" className="d-block w-100"/>
                        </picture>
                    </div>
                    <div className="carousel-item" data-bs-interval="2000">
                        <picture>
                            <source media="(min-width: 768px)" srcSet="images/banners/img.jpg"/>
                            <source media="(max-width: 768px)" srcSet="images/banners/sm/img.jpg"/>
                            <img src="images/banners/img.jpg" alt="regular pyramid built from four equilateral triangles" className="d-block w-100"/>
                        </picture>
                    </div>
                    <div className="carousel-item">
                        <picture>
                            <source media="(min-width: 768px)" srcSet="images/banners/img.jpg"/>
                            <source media="(max-width: 768px)" srcSet="images/banners/sm/img.jpg"/>
                            <img src="images/banners/img.jpg" alt="regular pyramid built from four equilateral triangles" className="d-block w-100"/>
                        </picture>
                    </div>
                </div>
                <a className="carousel-control-prev" href="#carouselExampleInterval" role="button" data-bs-slide="prev">
                    <span className="carousel-control-prev-icon icon-keyboard_arrow_left" aria-hidden="true"></span>
                    <span className="visually-hidden">Previous</span>
                </a>
                <a className="carousel-control-next" href="#carouselExampleInterval" role="button" data-bs-slide="next">
                    <span className="carousel-control-next-icon icon-keyboard_arrow_right" aria-hidden="true"></span>
                    <span className="visually-hidden">Next</span>
                </a>
            </div>
        </section>
    )
}

export default Carousel
