import React from 'react'

import {CURRENCY} from '../../config'


function ProductItem1({product}) {
    const {productImg, productName, productPrice, productSalesPrice} = product
    return (


        <div class="col-xxl-4 col-xl-4 col-lg-6 prod">
            <a href="">
                <div class="prod_img-cont">
                    <img class="img-fluid"
                        src={
                            `${
                                process.env.PUBLIC_URL
                            }/${productImg}`
                        }
                        alt=""/>
                </div>
                <div class="prod_prod-cont">
                    <title class="title">
                        {productName}</title>
                    <p class="prod_price">
                        <p class="prod_price">
                            {
                            `${CURRENCY}/${productPrice}`
                        }</p>
                    </p>
                    <p class="prod_sales">
                        {
                        `${CURRENCY}${productSalesPrice}`
                    }</p>
                    <p class="prod_amt-saved">You save {
                        `${CURRENCY}${
                            productPrice - productSalesPrice
                        }`
                    }</p>
                </div>
            </a>
        </div>

    )
}

export default ProductItem1
