import React, {useEffect} from 'react'

import Navbar, {NavbarAlert, NavbarContent, NavbarLink} from '../components/Navbar/';
import Footer, {NewsLetter} from '../components/Footer/';
import {Route, Switch, Redirect} from 'react-router-dom';
import HomeScreen from './HomeScreen';
import BlogScreen from './BlogScreen';
import ForgotPasswordScreen from './ForgotPasswordScreen';

// Redux
import {useSelector, useDispatch} from 'react-redux';
import {apiCallBegan} from '../store/api';


const alertMsg = " We are fully back in operation in Lagos & across the various states, keep your orders coming in. Kindly note that Payment on Delivery is NOT available. Thank you"
const promoImageSrcLg = "images/promo/top-promo/img-lg.gif";
const promoImageSrcSm = "images/promo/top-promo/img-sm.gif";

// Root Screen Component
function ScreenBase() {


    console.log('rendered')
    return (
        <> {/* Navbar || Compound Component */}


            {/* Routes */}
            <Navbar>
                <NavbarAlert alertMsg={alertMsg}
                    promoImageSrcSm={promoImageSrcSm}
                    promoImageSrcLg={promoImageSrcLg}/>
                <NavbarContent/>
                <NavbarLink/>
            </Navbar>


            {/* Routes */}
            <Switch>
                <Route exact path="/blog"
                    component={BlogScreen}/>


                <Route exact path="/"
                    component={HomeScreen}/>
                <Route exact path="/forgot-password"
                    component={ForgotPasswordScreen}/>

                <Redirect to="/drinksng/404"/>


            </Switch>


            {/* Footer || Compound Component */}
            <Footer>
                <NewsLetter/>
            </Footer>
        </>
    )
}

export default ScreenBase
