import React from 'react'
import Slider from "react-slick";


const settings = {

    slidesToShow: 1,
    mobileFirst: true,
    infinite: false,
    variableWidth: true,
    responsive: [
        {
            breakpoint: 1200,
           
        },

        {
            breakpoint: 10000,
            settings: 'unslick'
        }
    ]

};

function SliderBase({children}) {
    return (

        <Slider className="row" {...settings} >

            {children}
        </Slider>
    )
}

export default SliderBase
