import React from 'react'
import SliderBase from '../SliderBase';



function ProductList({products, itemType: ProductItem, type}) {
    const lg = type == 1 ? '' : '-lg';
    return (
        <div className={
            `prod_list-grid${lg} col-12`
        }>
            <div className="row">


                <SliderBase>
                {
                    products.map(product => (
                        <ProductItem product={product}/>
                    ))
                }
                </SliderBase>


                  
            </div>
        </div>
    )
}

export default ProductList
