import React from 'react'

function NavbarContent() {
    return (
        <header className="primary">
        <nav className="navbar navbar-expand-lg navbar-light">
            <div className="container-fluid">
                <a className="navbar-brand" href="#"><img src="images/logo.png" alt="" /></a>

                <div className="mobile-contact d-xxl-none d-xl-none  d-lg-none d-md-flex">
                    <a href="" className="btn btn-outline">
                        <span className="icon-whatsapp icon">
                            <span className="path1"></span><span className="path2"></span>
                        </span>
                        <span className="label"> whatsapp</span>
                    </a>
                    <a href="" className="btn btn-primary"><span className="icon-call icon"></span><span className="label">Call</span></a>
                </div>

                <div className="navbar-collapse" id="navbarSupportedContent">

                    <form className="d-flex">
                        <input className="form-control me-2" type="search" placeholder="What drink would you like to buy?" aria-label="Search"/>
                        <button className="btn btn-primary icon-search" type="submit"></button>
                    </form>

                    <ul className="navbar-nav mb-2 mb-lg-0 d-lg-flex d-md-none d-none">
                        <li className="nav-item dropdown contact-dropdown">
                            <a href="" className="whatsapp-link icon-whatsapp d-lg-none d-xl-block"><span className="path1"></span><span className="path2"></span></a>
                            <a className="nav-link dropdown-toggle contact-list" href="#" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                                <span className="contact-icon"><img src="images/numb-icon.jpg" alt="" className="img-fluid" /></span>
                                <span className="cont-numb d-lg-none d-xl-block d-none"> +234 818 218 0211</span>
                                <span className="cont-numb d-lg-block d-xl-none">Call</span>
                            </a>
                            <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
                                <li><a className="dropdown-item" href="#"><span className="icon icon-call"></span> +234 818 218 0211</a></li>
                                <li><a className="dropdown-item" href="#">
                                        <span className="icon icon-whatsapp">
                                            <span className="path1"></span><span className="path2"></span>
                                        </span>+234 818 218 0211</a></li>
                                <li><a className="dropdown-item" href="#"><span className="icon icon-email"></span> +234 818 218 0211</a></li>
                            </ul>
                        </li>

                        <li className="nav-item dropdown">
                            <a className="nav-link dropdown-toggle location" href="#" data-bs-toggle="modal" data-bs-target="#storePopup">
                                <span className="icon-stores"></span>
                                <span className="holder">
                                    <span className="location-label">My Location</span>
                                    <span className="location-state"> Lagos State (LA)</span>
                                </span>
                            </a>
                        </li>
                        <li className="nav-item d-lg-block">
                            <a className="nav-link account" href="#" tabIndex="-1" aria-disabled="true">
                                <span className="icon-user"></span>
                                <span className="label">My Account</span>
                            </a>
                        </li>
                        <li className="nav-item dropdown sm-cart-dopdown">
                            <a className="nav-link dropdown-toggle cart-sm" href="#" id="navbarDropdown2" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                                <span className="icon-cart"></span>
                                <span className="count">10</span>
                            </a>

                            <ul className="dropdown-menu dropdown-menu-end cart-dropdown" aria-labelledby="navbarDropdown2">
                                <li className="header">
                                    <h4 className="title">Cart</h4>
                                </li>
                                <li className="item">
                                    <a className="img-thum" href="#"><img src="images/products/img-1.jpg" alt="" className="img-fluid" /></a>
                                    <div className="cont">
                                        <h5 className="title"><span className="price">35,600.00</span><span className="qty">Qty 1</span> <a href="" className="icon-delete"></a></h5>
                                        <p><a href="">1 bottle of Johnnie Walker Black Label 70cl = Tote Bag + Mixers+ Cups</a></p>
                                    </div>
                                </li>
                                <li className="item">
                                    <hr />
                                </li>
                                <li className="item">
                                    <a className="img-thum" href="#"><img src="images/products/img-1.jpg" alt="" className="img-fluid" /></a>
                                    <div className="cont">
                                        <h5 className="title"><span className="price">35,600.00</span><span className="qty">Qty 1</span> <a href="" className="icon-delete"></a></h5>
                                        <p>1 bottle of Johnnie Walker Black Label 70cl = Tote Bag + Mixers+ Cups</p>
                                    </div>
                                </li>
                                <li className="total-cont">
                                    <h5 className="title"><span className="label">Total</span><span className="val">35,600.00</span></h5>
                                    <hr />
                                    <div className="cart-actions">
                                        <a href="" className="btn btn-outline">View Cart</a>
                                        <a href="" className="btn btn-success">Checkout</a>
                                    </div>
                                </li>

                            </ul>
                        </li>
                    </ul>

                </div>
            </div>
        </nav>
    </header>
    )
}

export default NavbarContent
