import React from 'react'

function NewsLetter() {
    return (
        <section class="newsletter">
            <div class="container-fluid">
                <div class="row">
                    <div class="col">
                        <span class="icon icon-email"></span>
                        <div class="cont">
                            <h4 class="title">Email Support</h4>
                            <small>help@drinks.ng</small>
                        </div>
                    </div>
                    <div class="col">
                        <span class="icon icon-call"></span>
                        <div class="cont">
                            <h4 class="title">Phone Support</h4>
                            <small>0700 882 9996
                            </small>
                        </div>
                    </div>
                    <div class="col-lg-3 col-md-6 latest">
                        <span class="icon icon-promo"></span>
                        <div class="cont">
                            <h4 class="title">Get Latest Deals</h4>
                            <small>Our best promotions sent to your inbox.</small>
                        </div>
                    </div>
                    <div class="col-xxl-5 col-xl-5 col-lg-5 col-md-12 col-xs-12">
                        <form class="d-flex">
                            <input class="form-control me-2" type="search" placeholder="Enter your email" aria-label="Search"/>
                            <button class="btn btn-primary icon-send" type="submit"></button>
                        </form>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default NewsLetter
