import {createSlice} from '@reduxjs/toolkit'
import {apiCallBegan} from './api';


const productSlice = createSlice({
    name: "products",
    initialState: {
        list: [],
        loading: false
    },
    reducers: {
        getAllProducts: function (products, action) {
            products.list.push(... action.payload)
            products.loading = false

        },
        startProductsLoading: function (products, action) {
            products.loading = true
        },
        stopProductsLoading: function (products, action) {
            products.loading = false
        }
    }
})


export const loadRecommendeProducts = () => {

    return apiCallBegan({
        onStart: startProductsLoading.type,
        payload: {
            method: 'get'
        },
        onSuccess: getAllProducts.type,
        onError: stopProductsLoading.type
    })
}


export const {getAllProducts, startProductsLoading, stopProductsLoading} = productSlice.actions

export default productSlice.reducer
