import React, {useMemo} from 'react'
import FormButton from '../components/Button/FormButton'
import FormInput from '../components/Input/FormInput'
import Joi from 'joi'
import useForm from '../customHooks/useForm'


function ForgotPasswordScreen() {

    const schema = useMemo(() => {
        return {email: Joi.string()}
    }, [])

    const fields = useMemo(() => {
        return {email: 'davik'}

    }, [])


    const [errors, onChange, onSubmit, values] = useForm(fields, schema)

    const handleSubmit = (e) => {
        e.preventDefault()
        if (onSubmit) {
            console.log('submitted')
        }
    }


    return (
        <section class="forgotpassword-page secure-access">
            <div class="container-fluid">
                <div class="row">
                    <div class="col-xxl-8 col-xl-10 col-lg-12 mx-auto">
                        <div class="row">
                            <div class="col-lg-6 main-cont">
                                <div class="cont">
                                    <h1 class="title">Forgot your password?</h1>
                                    <p>Lost your password? Enter your email and we will send you instructions on how to change your password.</p>
                                    <p class="singup-or-login">Remember your password?
                                        <a href="">Login with Drinks</a>
                                    </p>
                                </div>
                            </div>
                            <div class="col-lg-6 form-cont">
                                <form>
                                    <div class="mb-3">
                                        <FormInput onChange={onChange}
                                            name="email"
                                            label="Email"
                                            type="text"
                                            placeholder="Email"
                                            value={
                                                values.email
                                            }/>
                                        <div>{
                                            errors.email
                                        }</div>
                                    </div>
                                    <div class="d-grid mb-3">
                                        <FormButton onClick={handleSubmit}
                                            label="send"/>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default ForgotPasswordScreen
