import React from 'react'

import {CURRENCY} from '../../config'

function ProductItem2({product}) {
    const {productImg, productName, productPrice, productSalesPrice} = product
    return (
        <div class="col-xxl-3 col-xl-3 col-lg-4 prod">
            <a href="">
                <div class="prod_img-cont">
                    <img src={
                            `${
                                process.env.PUBLIC_URL
                            }/${productImg}`
                        }
                        class="img-fluid"
                        alt=""/>
                </div>
                <div class="prod_prod-cont">
                    <title class="title">
                        {productName}</title>
                    <p class="prod_price">
                        {
                        `${CURRENCY}/${productPrice}`
                    }</p>
                    <p class="prod_sales">
                        {
                        `${CURRENCY}${productSalesPrice}`
                    }</p>
                    <p class="prod_amt-saved">You save {
                        `${CURRENCY}${
                            productPrice - productSalesPrice
                        }`
                    }</p>
                </div>
            </a>
        </div>
    )
}

export default ProductItem2
