import React from 'react'
import PropTypes from 'prop-types'

function NavbarAlert({alertMsg, promoImageSrcLg, promoImageSrcSm}) {
    return (
        <>
          <section className="promo-top">
        <div className="alert alert-dismissible fade show" role="alert">
         <a href="" target="_blank">
             <picture>
                 <source media="(max-width: 600px)" srcSet={`${process.env.PUBLIC_URL}/${promoImageSrcSm}`}/>
                 <source media="(min-width: 799px)" srcSet={`${process.env.PUBLIC_URL}/${promoImageSrcLg}`}/>

                 <img src={`${process.env.PUBLIC_URL}/${promoImageSrcLg}`} alt="regular pyramid built from four equilateral triangles" className="d-block w-100" />
             </picture>
         </a>
         <button type="button" className="btn-close btn-close-white" data-bs-dismiss="alert" aria-label="Close"></button>
         </div>
     </section>
     <section className="alert-top d-none d-sm-none d-md-block d-lg-block">
         <div className="container-fluid">
             <div className="alert alert-dismissible fade show" role="alert">
                 <strong>Dear customer!</strong> {alertMsg}
                 <button type="button" className="btn-close btn-close-white" data-bs-dismiss="alert" aria-label="Close"></button>
             </div>
         </div>
     </section>
        </>
    )
}

NavbarAlert.prototype ={
   alertMsg : PropTypes.string,
   promoImageSrcSm: PropTypes.string,
   promoImageSrcLg: PropTypes.string

}

export default NavbarAlert;
