import React, {useEffect} from 'react'

// Redux
import {useDispatch, useSelector} from 'react-redux'
import {apiCallBegan} from '../store/api';
import {loadRecommendeProducts} from '../store/products'


// Custom Component
import Promo from '../components/Promo'
import Carousel from '../components/Home/Carousel';
import DeliverySettings from '../components/DeliverySettings';
import Brands from '../components/Home/Brands';

import ProductList from '../components/Product/ProductList';
import ProductItem2 from '../components/Product/ProductItem2';
import ProductItem1 from '../components/Product/ProductItem1';
import ProductLoader from '../components/Product/ProductLoader';


function HomeScreen() {


    const dealsProducts = [

        {
            productName: "1 bottle of Johnnie Walker Black Label 70cl = Tote Bag + Mixers+ Cup",
            productImg: "images/products/todaysdeals/img-1.jpg",
            productPrice: 35600,
            productSalesPrice: 35600

        }, {
            productName: "1 bottle of Johnnie Walker Black Label 70cl = Tote Bag + Mixers+ Cup",
            productImg: "images/products/todaysdeals/img-2.jpg",
            productPrice: 35600,
            productSalesPrice: 35600

        }, {
            productName: "1 bottle of Johnnie Walker Black Label 70cl = Tote Bag + Mixers+ Cup",
            productImg: "images/products/todaysdeals/img-3.jpg",
            productPrice: 35600,
            productSalesPrice: 35600

        }, {
            productName: "1 bottle of Johnnie Walker Black Label 70cl = Tote Bag + Mixers+ Cup",
            productImg: "images/products/todaysdeals/img-3.jpg",
            productPrice: 35600,
            productSalesPrice: 35600

        },
    ]

    const dispatch = useDispatch()
    const recommendedProducts = useSelector(store => store.products.list)
    const productsLoading = useSelector(store => store.products.loading)


    useEffect(() => {
        dispatch(loadRecommendeProducts())

    }, [])

    return (

        <div> {/* Carousel */}
            <Carousel></Carousel>
            <DeliverySettings/> {/* Recommended Products */}
            <section className="home-products" id="recommended">
                <div className="container-fluid">
                    <div className="row">


                        <div className="title-cont col-12">
                            <h4 className="title">Recommended
                            </h4>
                            <a href="">See All Items</a>
                        </div>

                        {
                        productsLoading && <ProductLoader/>
                    }

                        {
                        recommendedProducts.length > 1 && <ProductList type={1}
                            products={recommendedProducts}
                            itemType={ProductItem1}/>
                    } </div>
                </div>
            </section>


            <section className="prod_promo">
                <Promo promoImgSrc1={'images/promo/img-1.jpg'}
                    promoImgSrc2={'images/promo/img-2.jpg'}/>
            </section>

            {/* Today's Deals */}
            <section className="home-products" id="today_deals">
                <div className="container-fluid">
                    <div className="row">
                        <div className="title-cont col-12">
                            <h4 className="title">Todays Deals
                            </h4>
                            <a href="">See All Items</a>
                        </div>


                        {
                        productsLoading && <ProductLoader/>
                    }

                        {
                        recommendedProducts.length > 1 && <ProductList type={2}
                            products={dealsProducts}
                            itemType={ProductItem2}/>
                    } </div>
                </div>
            </section>


            {/* WholeSale */}
            <section className="home-products" id="wholesales">
                <div className="container-fluid">
                    <div className="row">
                        <div className="title-cont col-12">
                            <h4 className="title">WholeSale
                            </h4>
                            <a href="">See All Items</a>
                        </div>
                        {
                        productsLoading && <ProductLoader/>
                    }

                        {
                        recommendedProducts.length > 1 && <ProductList type={2}
                            products={dealsProducts}
                            itemType={ProductItem2}/>
                    } </div>
                </div>
            </section>


            {/* Product Promo */}

            <section className="prod_promo">
                <Promo promoImgSrc1={'images/promo/img-1.jpg'}
                    promoImgSrc2={'images/promo/img-2.jpg'}/>
            </section>


            {/* Best Sellers */}
            <section className="home-products" id="best-sellers">
                <div className="container-fluid">
                    <div className="row">
                        <div className="title-cont col-12">
                            <h4 className="title">Best Sellers
                            </h4>
                            <a href="">See All Items</a>
                        </div>
                    </div>
                    {
                    productsLoading && <ProductLoader/>
                }

                    {
                    recommendedProducts.length > 1 && <ProductList type={1}
                        products={recommendedProducts}
                        itemType={ProductItem1}/>
                } </div>
            </section>

            <Brands/>
        </div>

    )
}

export default HomeScreen
