import React from 'react'

import PropTypes from 'prop-types'

function Promo({promoImgSrc1, promoImgSrc2}) {
    return (
        <div className="container-fluid">
            <div className="row">
                <div className="col-lg-6 col-md-6 col-sm-6">
                    <a href=""><img src={
                                `${
                                    process.env.PUBLIC_URL
                                }/${promoImgSrc1}`
                            }
                            className="img-fluid"
                            alt=""/></a>
                </div>
                <div className="col-lg-6 col-md-6 col-sm-6">
                    <a href=""><img src={
                                `${
                                    process.env.PUBLIC_URL
                                }/${promoImgSrc2}`
                            }
                            className="img-fluid"
                            alt=""/></a>
                </div>
            </div>
        </div>
    )
}

Promo.prototype = {
    promoImgSrc1: PropTypes.string,
    promoImgSrc2: PropTypes.string
}

export default Promo
