import React from 'react'

function NotFound() {
    return (
        <section class="error404-page">
            <div class="container-fluid">
                <div class="row">
                    <div class="col-xl-6 col-lg-7 col-md-8 col-sm-10 mx-auto">
                        <div class="img-cont">
                            <img src="images/404-error-img.png" alt="" class="img-fluid"/>
                        </div>
                        <div class="cont text-center">
                            <h1 class="title">Whoooops</h1>
                            <p>We couldn't find the page you are looking for</p>
                            <a href="" class="btn btn-success">Countinue Shopping</a>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default NotFound
