import Joi from 'joi'
import React, {useState} from 'react'

function useForm (fields, schemaBluePrint){

    //  Pass in the input fields as an object and Pass in joi schema blue print as an object,

   
    const [values, setValues] = useState(fields)
    const [errors, setErrors] = useState({})


    const schema = Joi.object(schemaBluePrint)


    const onChange = (e)=> {
   
       const {error} =  SingleValidation(e)

       if(error){
           setErrors(errors => (
               {
                   ...errors,
                   [e.target.name] : error.details[0].message
               }
           ))

          
       }else {
        setErrors(errors => (
            {
                ...errors,
                [e.target.name] : ""
            }
           ))
       }



        setValues(values => (
            {
                ...values,
                [e.target.name] : e.target.value
            }
        ))

    }

   const  onSubmit = () => {
      
        const {error} =  schema.validate(values, {abortEarly: false})

        if(error) {
            let errorObj = {}
           error.details.forEach((error, index, array) => {

            
                   errorObj[error.path[0]] = error.message
               
                
    
            })

            setErrors(errors => (
                {
                    ...errorObj
                }
            ))

            return false;
        }

    

            return true

    }


    const SingleValidation = (e) => {
       
         const singleShema = Joi.object({
             [e.target.name]: schemaBluePrint[e.target.name]
         })

         const result = singleShema.validate(
             {
                 [e.target.name] : e.target.value
             }
         )

         return result

         

    }

// Returns, 1 error object,  2 onChange Event handler,  3 onSubmit event handler and  4 values 
    return [errors, onChange, onSubmit, values ]


}

export default useForm



